@import "../../assets/styles/variables.scss";
.product-page-error {
	padding: 32px 0;
	// .product-page-error__inner
	&__inner {
		text-align: center;
	}
	// .product-page-error__image
	&__image {
		display: block;
		max-width: 33.5rem;
		margin: 0 auto 1em;
		img {
			max-width: 100%;
		}
		@media (max-width: 48em) {
			max-width: 80%;
		}
		@media (max-width: 30em) {
			max-width: 100%;
		}
	}
	// .product-page-error__title
	&__title {
		max-width: 66rem;
		margin: 0 auto 0.125em;
		font-size: 4rem;
		line-height: 112%;
		color: $mainDarkColor;
		@media (max-width: 62em) {
			font-size: 3rem;
			max-width: 80%;
		}
		@media (max-width: 48em) {
			font-size: 2.5rem;
			max-width: unset;
		}
		@media (max-width: 30em) {
			font-size: 2rem;
		}
	}
	// .product-page-error__subtitle
	&__subtitle {
		font-size: 1.25rem;
		line-height: 112%;
		color: $mainDarkColor;
		margin-bottom: 2em;
		@media (max-width: 62em) {
			margin-bottom: 1.25em;
		}
	}
	// .product-page-error__button
	&__button {
	}
}
