@import "../../assets/styles/variables.scss";
.page-product {
	// .page-product__top
	&__top {
		padding: 60px 0 120px;
		display: flex;
		align-items: flex-start;
		gap: 40px;
		@media (max-width: 62em) {
			padding: 20px 0 60px;
			gap: 20px;
		}
		@media (max-width: 48em) {
			flex-direction: column;
			align-items: center;
		}
	}
	// .page-product__media
	&__media {
		position: relative;
		flex: 0 0 584px;
		@media (max-width: 75em) {
			max-width: 30rem;
		}
		@media (max-width: 62em) {
			display: flex;
			flex-direction: column-reverse;
			gap: 10px;
			flex: 1 0 50%;
		}
		@media (max-width: 48em) {
			flex-direction: row-reverse;
			justify-content: center;
			align-items: center;
		}
		@media (max-width: 30em) {
			flex-direction: column-reverse;
		}
	}
	// .page-product__labels
	&__labels {
		@media (max-width: 48em) {
			flex: 0 0 75px;
		}
		@media (max-width: 30em) {
			flex: 1 1 auto;
			.product-card-label {
				width: unset;
				min-width: 2.5rem;
				height: unset;
				font-size: 1rem;
				border-radius: 1em;
			}
		}
	}
	// .page-product__image
	&__image {
		img {
			max-width: 100%;
			border-radius: 50%;
			@media (max-width: 30em) {
				max-width: 100%;
			}
		}
	}
	// .page-product__content
	&__content {
		@media (max-width: 48em) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			gap: 1rem;
		}
		@media (max-width: 30em) {
			width: 100%;
		}
	}
	// .page-product__title
	&__title {
		font-size: 4rem;
		font-weight: 400;
		line-height: 112%;
		color: $mainDarkColor;
		margin-bottom: 0.1875em;
		@media (max-width: 62em) {
			font-size: 3rem;
		}
		@media (max-width: 48em) {
			display: none;
		}
		&--mobile {
			display: none;
			@media (max-width: 48em) {
				display: block;
				margin-bottom: 0;
			}
			@media (max-width: 30em) {
				font-size: 2.5rem;
			}
		}
	}
	// .page-product__button
	&__button {
		margin-bottom: 1.66em;
		@media (max-width: 48em) {
			margin-bottom: 0;
		}
		@media (max-width: 30em) {
			grid-column: 1 / 3;
			grid-row: 2 / 3;
			white-space: unset;
		}
	}
	// .page-product__sizes
	&__sizes {
		margin-bottom: 2em;
		@media (max-width: 48em) {
			margin-bottom: 0;
			grid-column: 1 / 2;
			grid-row: 1 / 2;
			justify-self: center;
		}
		@media (max-width: 30em) {
			grid-column: 1 / 3;
		}
	}
	// .page-product__crunches
	&__crunches {
		margin-bottom: 2em;
		@media (max-width: 48em) {
			margin-bottom: 0;
			grid-column: 2 / 3;
		}
		@media (max-width: 30em) {
			grid-column: 1 / 3;
		}
	}
	// .page-product__actions
	&__actions {
		display: grid;
		grid-template-columns: 11.9rem 1fr;
		gap: 20px;
		align-items: flex-end;
		@media (max-width: 75em) {
			grid-template-columns: 8rem 1fr;
			gap: 10px;
		}
		@media (max-width: 62em) {
			grid-template-columns: 1fr;
			justify-items: center;
		}
		@media (max-width: 48em) {
			grid-column: 1 / 3;
		}
	}
	// .page-product__prices
	&__prices {
		display: flex;
		flex-direction: column;
		gap: 4px;
		@media (max-width: 62em) {
			flex-direction: row;
			align-items: center;
			gap: 20px;
		}
	}
	// .page-product__price
	&__price {
		white-space: nowrap;
		// .page-product__price--old
		&--old {
			font-size: 2rem;
			color: $priceColor;
			text-decoration: line-through;
			@media (max-width: 75em) {
				font-size: 1.5rem;
			}
		}
		// .page-product__price--current
		&--current {
			font-size: 4rem;
			color: $mainDarkColor;
			@media (max-width: 75em) {
				font-size: 2.5rem;
			}
		}
	}
	// .page-product__user-actions
	&__user-actions {
		justify-self: flex-end;
		padding-bottom: 8px;
		@media (max-width: 75em) {
			padding-bottom: 4px;
		}
		@media (max-width: 62em) {
			justify-self: unset;
			.user-actions__button {
				font-size: 1.5rem;
			}
		}
		@media (max-width: 48em) {
			width: 100%;
			padding-bottom: 0;
			.user-actions {
				max-width: unset;
			}
			.user-actions__button {
				font-size: 1.25rem;
			}
			.add-to-cart-button.adding .add-to-cart-button__dots {
				animation-duration: 2.2s;
			}
		}
	}
	// .page-product__bottom
	&__bottom {
		position: relative;
		padding: 60px 0;
		background-color: $hoverBackgroundColor;
		@media (max-width: 48em) {
			padding: 30px 0;
		}
		&::before {
			content: "";
			position: absolute;
			bottom: -50px;
			left: 0;
			width: 100%;
			height: 74px;
			background-image: url(../../assets/images/page-product/bg-waves.png);
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
		}
	}
	// .page-product__description
	&__description {
	}
	// .page-product__advertisement
	&__advertisement {
		@media (max-width: 62em) {
			padding-top: 50px;
		}
	}
}
.product-info {
	max-width: 50rem;
	margin: 0 auto;
	// .product-info__title
	&__title {
		font-size: 2rem;
		font-weight: 400;
		line-height: 125%;
		color: $priceColor;
		margin-bottom: 0.375em;
	}
	// .product-info__nutritional-value
	&__nutritional-value {
		line-height: 140%;
		span {
			color: $priceColor;
		}
	}
	// .product-info__ingredients
	&__ingredients {
		margin: 0.6em 0;
	}
	// .product-info__description
	&__description {
		line-height: 140%;
		color: #7e7e7e;
	}
}
