@import "../../assets/styles/variables.scss";
.crunch-item {
	display: flex;
	align-items: center;
	flex: 0 0 7rem;
	flex-direction: column;
	row-gap: 0.75em;
	cursor: pointer;
	text-align: center;
	&:hover {
		.crunch-item__image {
			border: 2px solid $accentColor;
		}
		.crunch-item__image img {
			transform: scale(0.9) rotate(20deg);
			top: 0;
			left: 0;
		}
	}
	&:active {
		.crunch-item__image {
			border: 2px solid $buttonActiveColor;
			transition: border 0.3s ease-in-out;
		}
		.crunch-item__title {
			color: $buttonActiveColor;
			transition: color 0.1s ease-in-out;
		}
	}
	&[data-disabled="true"] {
		pointer-events: none;
	}
	// .crunch-item__input
	&__input {
		&:checked + .crunch-item__image {
			border: 2px solid $accentColor;
			&::before,
			&::after {
				opacity: 1;
				visibility: visible;
				transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
			}
			img {
				transform: scale(0.9) rotate(20deg);
				top: 0;
				left: 0;
			}
		}
		&:focus-visible + .crunch-item__image {
			border: 2px solid $blue1Color;
			&::before {
				opacity: 1;
				visibility: visible;
				background-color: $blue1Color;
			}
			&::after {
				opacity: 1;
				visibility: visible;
				background-color: #ffffff;
			}
			img {
				transform: scale(0.9) rotate(20deg);
				top: 0;
				left: 0;
			}
		}
		&:disabled + .crunch-item__image {
			filter: grayscale(0.8);
		}
		&:checked:disabled + .crunch-item__image {
			border: 2px solid #d3cdcd;
			&::before {
				background-color: #d3cdcd;
			}
		}
	}
	// .crunch-item__image
	&__image {
		display: block;
		position: relative;
		width: 5rem;
		height: 5rem;
		border-radius: 50%;
		border: 2px solid transparent;
		transition: filter 0.3s ease-in-out;
		@media (max-width: 62em) {
			width: 4rem;
			height: 4rem;
		}
		&::before {
			content: "";
			z-index: 1;
			position: absolute;
			left: 50%;
			bottom: -0.625rem;
			transform: translateX(-50%);
			width: 1.25rem;
			height: 1.25rem;
			border-radius: 50%;
			border: 0.25rem solid #ffffff;
			background-color: $accentColor;
			opacity: 0;
			visibility: hidden;
		}
		&::after {
			content: "";
			z-index: 1;
			position: absolute;
			left: 50%;
			bottom: -0.1875rem;
			transform: translateX(-50%);
			width: 0.375rem;
			height: 0.375rem;
			border-radius: 50%;
			background-color: #ffffff;
			opacity: 0;
			visibility: hidden;
		}
		img {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			border-radius: 50%;
			transition: transform 0.3s ease-in-out;
		}
	}
	// .crunch-item__title
	&__title {
		display: block;
		font-size: 1.25rem;
		line-height: 1;
		color: $mainDarkColor;
	}
}
